.navbar-container {
	position: sticky;
	display: flex;
	align-items: center;
	justify-content: space-around;
	top: 0;
	left: 0;
	background-color: var(--White);
	font-weight: 400;
	z-index: 10;
}

.img-nav {
	width: 12%;
	height: 12%;
}

.links {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 20px;
	margin-right: 50px;
	margin-left: 50px;
}
